.uploadContainer,
.funderType {
    width: "100%";
    margin-right: auto;
    margin-left: auto;
    padding: 10px;
    text-align: center;
}

.funderType {
    margin-top: 60px
}

.mainContainer {
    padding: 20px;
    text-align: center;
    border-radius: 10px;
    width: 800px;
    border: #bbb dashed 2px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 20px
}

.center {
    padding: 20px;
    text-align: center;
    width: 800px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 20px
}

.funderType label {
    margin: 10px;
    cursor: pointer;
    font-size: 14px;

}

.funderType input[type=radio] {
    margin-right: 5px;
    cursor: pointer;
}

.disabled {
    pointer-events: none;
    opacity: 0.3 !important;
}